body {
  background-color: #000000;
}

.App {
  background-color: #660099;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input {
  background-color: rgb(27, 112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #ffffff;
}

#to-do-form input::placeholder {
  color: #ffffff;
}


#to-do-form input:focus {
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 10px;
  background-color: #ff6600;
  border: 0;
  color: #ffffff;
  cursor: pointer;
  font-size: 18px;
}

#to-do-form button:focus{
  outline: none;
}